import {Dimensions, PixelRatio} from 'react-native';

const WIDTH_IPHONE_14 = 390;

export const {height: ScreenHeight, width: ScreenWidth} =
  Dimensions.get('window');

// use when Left/Right/Width
export const SizeDP = (widthSize: number) => {
  const screenRatio = ScreenWidth / WIDTH_IPHONE_14;

  // Adjust based on whether the device is an iPad or not
  if (ScreenWidth >= 768 && ScreenHeight >= 1024) {
    // Assuming itâ€™s an iPad, apply a specific scale factor
    return PixelRatio.roundToNearestPixel(widthSize * screenRatio * 0.65); // You can adjust the scale factor if needed
  } else {
    // Standard for iPhone or smaller devices
    return PixelRatio.roundToNearestPixel(widthSize * screenRatio);
  }
};

export const ratioH_W = ScreenHeight / ScreenWidth;

const FontSize = {
  // Font size
  /** font size 10px */
  FontTiniest: SizeDP(10),
  FontTinier: SizeDP(11),
  /** font size 12px */
  FontTiny: SizeDP(12),
  FontSmallest: SizeDP(13),
  /** font size 14px */
  FontSmall: SizeDP(14),
  /** font size 16px */
  FontBase: SizeDP(16),
  FontMediumPlus: SizeDP(17),
  /** font size 18px */
  FontLarge: SizeDP(18),
  FontBigger: SizeDP(20),
  FontBigBigger: SizeDP(22),
  FontBiggest: SizeDP(24),
  FontHuge: SizeDP(26),
  FontLarge3x: SizeDP(28),
  FontHuger: SizeDP(30),
  FontHugest: SizeDP(32),
  FontGreat: SizeDP(34),
  FontMidGreater: SizeDP(36),
  FontGreater: SizeDP(38),
  FontGreatest: SizeDP(60),
};

export default FontSize;
